import React from "react"
import ReactDiffViewer, { DiffMethod } from "react-diff-viewer"
class ML_DiffViewer extends React.Component {
  render() {
    const { oldValue, newValue } = this.props
    return (
      <ReactDiffViewer
        oldValue={oldValue}
        newValue={newValue}
        splitView={true}
        compareMethod={DiffMethod.WORDS}
        {...this.props}
      />
    )
  }
}

export default ML_DiffViewer
